import React, {Component} from 'react';
import './style.css'

class Days extends Component{
    render (){
      return (
            <div>
                <img className="WarningImg1" src="http://www.stickpng.com/assets/images/5a81af7d9123fa7bcc9b0793.png" alt="img"></img>
                <h5>{this.props.day}</h5>
                <img className="WeatherImg1" src="https://cdn.iconscout.com/icon/free/png-256/brightness-summer-sun-sunny-hot-38953.png" alt="img"></img>
                <p>{this.props.temp}℃</p>
            </div>
            )
    }

}

export default Days;