import React, {Component} from 'react';
import './style.css';
import Cloud from '../Images/Weather_Icons/PartlyCloudy.png';
import Sun from '../Images/Weather_Icons/Sun.png';
import Rain from '../Images/Weather_Icons/Heavy Rain.png';

class Icon extends Component{
  render (){
    let icony = (e) => {
      if(e==="Sunny"){
        console.log("Success Sun");
       return(Sun);
      }
      else if(e === "Rain"){
        console.log("Success Rain");
        return(Rain);
       
      }
       
      else if(e==="Clouds"){
        console.log("Cloud");
        return(Cloud);
      }
    }
    let description = icony(this.props.description);  
    console.log(description);
  
    return (
      <div className="Icon">
         <img className="IconImg" src={description} alt="asd"></img>
      </div>
    )
  
  }

}

export default Icon;