import React, {Component} from 'react';
import Days from './Days.js';
import './style.css'

class LaterDays extends Component{
  render (){
  
    return (
      <div className="LaterDays">
        <Days className="DayOne" day={this.props.day1} temp={this.props.temp1}/>
        <Days className="DayTwo" day={this.props.day2} temp={this.props.temp2}/>
        <Days className="DayThree" day={this.props.day3} temp={this.props.temp3}/>
        <Days className="DayFour" day={this.props.day4} temp={this.props.temp4}/>
        <Days className="DayFive" day={this.props.day5} temp={this.props.temp5}/>
      </div>
    )
  
  }

}

export default LaterDays;