import React, {Component} from 'react';
import Information from './Information.js'
import './style.css';
import COF from '../Images/Normal_icons/COF.png'
import Precipitation from '../Images/Normal_icons/Precipitation.png'
import wind from '../Images/Normal_icons/wind.svg'
import visibility from '../Images/Normal_icons/visibility.svg'

class HighInfo extends Component{

  render (){
  
    return (
      <div className="HighInfo">
        <Information className="Visibility" name="Visibility" percent={this.props.visibility} img={visibility}/>
        <Information className="WindSpeed" name="Wind Speed" percent={this.props.windspeed} img={wind}/>
        <Information className="Precipitation" name="Precipitation" percent={this.props.precipitation} img={Precipitation}/>
        <Information className="ChanceOfRain" name="Chance Of Rain" percent={this.props.cof} img={COF}/>
      </div>
    ) 
  }
}

export default HighInfo;