import React, {Component} from 'react';
import Information from './Information.js';
import './style.css';
import PressureImg from '../Images/Normal_icons/pressure.svg'
import UVImg from '../Images/Normal_icons/UV.png'
import HumidityImg from '../Images/Normal_icons/humidity (1).svg'

class LowInfo extends Component{

  render (){
  
    return (
      <div className="LowInfo">
        <Information className="Pressure" name="Pressure" percent={this.props.pressure} img={PressureImg}/>
        <Information className="Humidity" name="Humidity" percent={this.props.humidity} img={HumidityImg}/>   
        <Information className="UVIndex" name="UV Index" percent="45" img={UVImg}/>
      </div>
    )
  
  }

}

export default LowInfo;