import React, {Component} from 'react';
import './style.css'
/*<input className="Slide" type="range" min="1" max="100" value="50" id="myRange"></input>*/

class Slider extends Component{
  constructor() {
    super()
    this.state = {
        time: 0,
        temp: 4,
        conditions: "Sunny Icon",
    }
    this.handleChange = this.handleChange.bind(this)
  }

  handleChange(event) {
    this.setState({time: event.target.value});
  }

  render (){
  
    return (
      <div className="Slider">
          <p className="SlideTime">{this.state.time}:00</p>
          <p className="SlideTemp">{this.state.temp}℃</p>
          <p className="SlideConditions">{this.state.conditions}</p>
            <input className="Slide" 
                    value={this.state.time} 
                    name="temp" 
                    onChange={this.handleChange}
                    type="range"  min="00" max="23"/>
      </div>
    )
  
  }

}

export default Slider;
