import React, {Component} from 'react';
import './style.css'

class Temperature extends Component{
  render (){
  
    return (
      <div className="Temperature">
          <h1>{this.props.temperature} </h1>
          <p>H: {this.props.hightemp} / L: {this.props.lowtemp}</p>
      </div>
    )
  
  }

}

export default Temperature;
