import React, {Component} from 'react';
import './style.css'

class Location extends Component{
  render (){
    const gSpot = (e) => {
      e.preventDefault();
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(showPosition);
      } else { 
        console.log = "Geolocation is not supported by this browser.";
      }
    }
    const showPosition = (position) => {
      document.getElementsByName('lat')[0].value = position.coords.latitude ; 
      document.getElementsByName('long')[0].value =position.coords.longitude;
      document.getElementById('clickButton').click();
    }
    window.onload = gSpot;
    let d = new Date();
    let days = ["Sunday","Monday","Tuesday","Wednesday","Thursday","Friday","Saturday"];
    let  months = ["January","February","March","April","May","June","July","August","September","October","November","December"];
let f_date = d.getDate() + " " + months[d.getMonth()] +" "+ d.getFullYear() + " " + days[d.getDay()] + " " + d.getHours() + ":" + d.getMinutes();
    return (
      <div className="Location">
          <form onSubmit={this.props.getLocation}>
          <input name="lat"/>
          <input name="long"/>
          <button id="clickButton">Get weather</button>
          </form>
          <img className="LocationImg" src="https://www.shoplondonsquare.com/wp-content/plugins/imaginuity-centers3/js/themes/JLL/lib/img/store-location-icon.png" alt="img"></img>
          <h2>{this.props.cityname}</h2>
          <p>{f_date}</p>
      </div>
    )
  
  }

}

export default Location;